<template>
    <div style="padding-top:10px;">
        <div class="title"></div>
        <div class="table-box">
            <div class="my-table-header">
                <!-- <span  style="width: 40px; text-align: center;"></span> -->
                <span  style="width: 90px; text-align: center;"></span>
                <span style="width: 70px; text-align: center;"></span>
                <span style="width: 70px; text-align: center;"></span>
            </div>
            <div class="my-table-body">
                <vue-seamless-scroll :data="tableData" class="seamless-warp"  :class-option="classOption">

                <a
                    href="javascript:;"
                    v-for="(item, index) in tableData"
                    :key="index"
                    class="my-table-content"
                >
                    <!-- <div class="my_currentPage" style="width: 40px;">{{ index+1 }}</div> -->
                    <div class="my_currentPage" style="width: 90px; overflow: hidden;text-overflow:ellipsis;white-space: nowrap;">{{ item.name }}</div>
                    <div class="my_currentPage" style="width: 60px;">{{ item.check }}</div>
                    <div class="my_currentPage" style="width: 65px;"></div>
                </a>
                </vue-seamless-scroll>
            </div>
        </div>
    </div>
</template>

<script>
import {http} from '@/utils/request'
export default {
    data() {
        return {
            tableData: [],
        }
    },
    created() {
        this.requestTableData()
    },
    methods: {
        requestTableData() {
            let json = {
                username: "api", 
                password: "a123456b"
            }
            http.get('https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/water/sections', {params: {json}})
            .then(res => {
                this.tableData = res.data.result;
                this.tableData = this.tableData.filter(item=>{
                    return item.type == '' || item.type == ''
                })
            })
        },
    },
    computed:{
        classOption() {
            return {
            step: .5, // 
            limitMoveNum: 10, //  this.dataList.length
            hoverStop: true, // stop
            direction: 1, // 0 1 2 3
            // openWatch: true, // dom
            singleHeight: 0, // (0) direction => 0/1
            singleWidth: 0, // (0) direction => 2/3
            waitTime: 1000, // (1000ms)
            };
        },
    }
}
</script>


<style lang="scss" scoped>
@import '../../styles/monthly.scss';
.title {
    width: 197px;
        height: 46px;
        text-align: center;
        line-height: 46px;
        font-size: 21px;
        background:url('~@/assets/center-box-title.png');
}
</style>